// src/styles/BakeryMap/SideBarStyles.js
import styled from 'styled-components';

export const Container = styled.div`
  background-color: white;
`;

export const SideBarComponent = styled.div`
  position: fixed;
  z-index: 20;
  background-color: white;
  color: #202020;
  top: 0;
  left: 0;
  bottom: 0;
  border-right: 4px solid #202020;
  transition: 0.4s ease;
  height: 100%;
  width: ${({ width }) => `${width}px`}; 
  transform: ${({ $xPosition }) => `translatex(${$xPosition}px)`};  
`;
//React    props  custom props  , custom props    Propery   . so,    $ 


/*
position: relative           .   absolute     . 
, SideBarButton relative , Content   relative         -> SideBarButton    .
so, SideBarButton position absolute .   SideBarButton    Content  .  .*/
export const SideBarButton = styled.button`
  position: absolute; 
  z-index: 20;
  border: 4px solid #202020;
  border-radius: 40px;
  height: 50px;
  width: 50px;
  //top: 10px;
  top: 50%; //   
  //left: 360px;
  left: ${({ width }) => `${width}px`}; //   + 10    . left        .
  transition: 0.8s ease;
`;

export const ButtonImg = styled.img`
  width: 100%;
  height: 100%;
  pointer-events: none;
`;

//   <h2> 
export const SideBarTitle = styled.h2`
    text-align: center;
    padding: 50px 0;
    font-family: 'OurFont1', sans-serif; /*   */
    font-size: 30px;
    font-weight: bold;
    color: black;
    background-color: #F4A460;   
`;
//{/*position: fixed; //top: 0; left: 0; width: 100%;
//z-index: 100; /*       */
//margin: 0; box-sizing: border-box; */}

export const CurrentLocation = styled.p`
    text-align: center;
    padding: 15px 0;
    font-size: 9px;
    color: black;
    background-color: #white;
    border-bottom: 6px double #ddd;
`;


//    
export const Content = styled.div`
  position: relative;
  width: calc(100% - 40px);
  height: calc(100% - 60px); 
  padding: 40px 20px 0 20px;
  overflow-y: auto; /**/
`;

export const ListItem = styled.li`
  list-style: none;
  margin-bottom: 20px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 8px;
  background-color: #fff;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  display: flex;
  align-items: center;
`;

export const ListItemImg = styled.img`
  width: 60px;
  height: 60px;
  //border-radius: 50%;
  margin-right: 15px;
  object-fit: cover;
`;

export const ListItemInfo = styled.div`
  display: flex;
  flex-direction: column;
`;

export const ListItemTitle = styled.h5`
  margin: 0;
  font-size: 16px;
  font-weight: bold;
  color: #333;
`;

export const ListItemAddress = styled.span`
  font-size: 14px;
  color: #777;
`;

export const ListItemDistance = styled.span`
  font-size: 12px;
  color: #999;
`;