import styled from 'styled-components';
import backgroundImage from '../assets/images/ClickerGameMainScreen.png';
import tutorial from '../assets/images/ClickerGameTutorialButton.png';

export const Center = styled.div`
  width: 100%;
  height: 100vh;
  display: flex; /* Flexbox  */
  flex-direction: column; /*   */
  justify-content: center; /*  :  */
  align-items: center; /*  :  */
  background-image: url(${backgroundImage});
  background-size: cover; /*     */
  background-position: center; /*   */
  position: relative; /*       */
`;

export const Main = styled.main`
  width: 100%;
  height: 100%;
`;

export const TutorialButton = styled.div`
  z-index: 100;
  width: min(20vw, 20vh); /*         */
  height: min(20vw, 20vh); /*         */
  background-image: url(${tutorial});
  background-size: contain; /*      */
  background-repeat: no-repeat; /*     */
  background-position: center; /*    */
  position: absolute; /*    */
  top: 30vh; /*  20vh  */
  left: 20vw; /*  20vw  */
`;


export const Chapter = styled.div`
  position: relative; /*      */
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 100px; /*    */
  max-width: 1200px; /*    */
`;


export const ChapterContainer = styled.div`
  position: relative; /*      */
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 100px; /*    */
  max-width: 1200px; /*    */
  left: 10vw; /*  20vw  */
  top: -20vh;
`;

export const ChapterItem = styled.img`
  position: relative;
  width: clamp(7vw, 9%, 130px); /* ,    */
  height: auto; /*     */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /*   */

  /*      */
  @media (max-width: 768px) {
    margin: 5px; /*     */
  }
`;

export const TutorialBackground = styled.div`
  width: 100%;
  height: 100vh;
  display: flex; /* Flexbox  */
  flex-direction: row; /*   */
  justify-content: center; /*  :  */
  align-items: flex-end; /*  :   */
  background-image: ${(props) => `url(${props.bgImage})`};
  background-size: cover; /*     */
  background-position: center; /*   */
  position: relative; /*       */
  gap: 20px; /*    */

`;

export const TutorialMessage = styled.div`
  width: 75%;
  height: 12%;
  display: flex; /* Flexbox  */
  justify-content: center; /*  :  */
  align-items: center; /*  :  */
  background-color: rgba(255, 255, 255, 0.5); /*    */
  border-radius: 50px; /*   */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /*    */
  color: black; /*   */
  font-size: min(3vw, 3vh);
  font-family: 'OurFont2'; /*   */
  text-align: center; /*    */
  padding: 10px; /*   */
  margin-bottom: 2%; /*   */
`;

export const NextButton = styled.div`
  width: 10%;
  height: 12%;
  display: flex; /* Flexbox  */
  justify-content: center; /*  :  */
  align-items: center; /*  :  */
  background-color: rgba(255, 255, 255, 0.7); /*    */
  border-radius: 50px; /*   */
  color: black; /*   */
  font-size: min(3vw, 3vh);
  font-family: 'OurFont2'; /*   */
  text-align: center; /*    */
  padding: 10px; /*   */

  margin-bottom: 2%; /*   */
`;