// src/components/BakeryMap/SideBar.js
import React, { useEffect, useRef, useState } from "react";
import { Container, SideBarComponent, SideBarButton, Content, ButtonImg, SideBarTitle, CurrentLocation, ListItem, ListItemImg, ListItemInfo, ListItemTitle, ListItemAddress, ListItemDistance } from "../../styles/BakeryMap/SideBarStyles";
import sideBarBtn from "../../assets/images/BakeryMap/sideBarBtn.png";
import closeBtn from "../../assets/images/BakeryMap/closeBtn.png";
import listItemImg from "../../assets/images/BakeryMap/marker2.png";

//places prop =    
const SideBar = ({ width = 280, places }) => {
    const [isOpen, setOpen] = useState(false); //  /   
    const [xPosition, setX] = useState(width); //  x    
    const side = useRef(); //  DOM     side (ref)  

    // button   
    // /   
    const toggleMenu = () => {
        if (xPosition > 0) { //  
            setX(0); //xPosition 0 ( )
            setOpen(true);
        } else {
            setX(width); // 
            setOpen(false);
        }
    };

    //  ()      
    const handleClose = async e => {
        let sideArea = side.current; //side ref   DOM 
        //sideChildren:   (e.target)    
        let sideCildren = side.current.contains(e.target);

        //  &&    ,   
        if (isOpen && (!sideArea || !sideCildren)) {
            //   ()  await .
            await setX(width); //setX()  x    
            await setOpen(false); // setOpen()  /   .
            // ,     ,  /       async/await .
        }
    }

    // useEffect:   /  handleClose  window     / 
    useEffect(() => {
        //  ( )     ->    -     handleClose  
        window.addEventListener('click', handleClose);
        //           .   
        //cleanup  -     handleClose   
        return () => {
            window.removeEventListener('click', handleClose);
        };
    })

    //  ,     jsx .    & ->   

    return (
        <Container>
            {/* side ref       */}
            {/* transform     x       */}
            <SideBarComponent ref={side} width={width} $xPosition={-xPosition}>
                {/*   -> toggleMenu()  -> setX,setOpen  ->  ->  /  */}
                <SideBarButton onClick={toggleMenu} width={width + 10}>
                    {isOpen ?
                        <ButtonImg src={closeBtn} alt="btn" /> : <ButtonImg src={sideBarBtn} alt="btn" />
                    }
                </SideBarButton>
                {/*   places     */}
                {/*   */}
                <SideBarTitle>
                       
                </SideBarTitle>
                {/*    ,  api . . */}
                <CurrentLocation> ~~~
                      </CurrentLocation>
                {/* ButtonContainer  */}
                <Content>
                    <ul>
                        {places.map((place, index) => (
                            <ListItem key={index}>
                                <ListItemImg src={listItemImg} alt={place.place_name} />
                                <ListItemInfo>
                                    <ListItemTitle>{place.place_name}</ListItemTitle>
                                    <ListItemAddress>{place.address_name}</ListItemAddress>
                                    <ListItemDistance>{place.distance}m</ListItemDistance>
                                </ListItemInfo>
                            </ListItem>
                        ))}
                    </ul>
                </Content>
            </SideBarComponent>
        </Container>
    );
};

export default SideBar;