import React, { useEffect, useState, useRef } from "react";
import RoadView from "./RoadView";
import markerImg from "../../assets/images/BakeryMap/marker2.png";
import CustomOverlayContent from "./CustomOverlayContent";
import ReactDOMServer from 'react-dom/server';
import currentLocationImg from "../../assets/images/BakeryMap/currentLocation2.png";
import ZoomControlBtn from './ZoomControlBtn';
import MapTypeControlBtn from './MapTypeControlBtn';

const { kakao } = window; // Kakao API    window  kakao .


const CustomMap = ({ setPlaces }) => {
    const [roadViewPlace, setRoadViewPlace] = useState(null); //       
    const [roadViewPosition, setRoadViewPosition] = useState(null);
    //const [pagination, setPagination] = useState(null); //   .      .
    const [currentPosition, setCurrentPosition] = useState(null);
    const mapRef = useRef(null); // map    useRef 
    const markersRef = useRef([]); //    useRef 
    const currentMarker = useRef(null); //   

    //    .   &   
    const handleCurrentLocation = () => {
        navigator.geolocation.getCurrentPosition((position) => {
            const currentLat = position.coords.latitude;
            const currentLng = position.coords.longitude;
            const currentCoordinate = new kakao.maps.LatLng(37.58284829999999, 127.0105811);
            //   
            if (currentMarker.current) {
                currentMarker.current.setMap(null);
            }
            //    
            currentMarker.current = new kakao.maps.Marker({
                map: mapRef.current,
                position: currentCoordinate,
                title: " ",
                zIndex: 10
            });

            //      
            mapRef.current.panTo(currentCoordinate);

            //    
            setCurrentPosition(currentCoordinate);
        }, showErrorMsg, {
            enableHighAccuracy: true, //    
            maximumAge: 30000, // 30     
            timeout: 27000 // 27       
        });
    };


    useEffect(() => {
        const container = document.getElementById('customMap'); //    DOM  
        const lat = 37.58284829999999; //  <>
        const lng = 127.0105811; // 
        const options = {
            center: new kakao.maps.LatLng(lat, lng), //     map .
            level: 3 // map  (high=wide) default: 4
        };

        const map = new kakao.maps.Map(container, options); //   .
        mapRef.current = map; // map  useRef  
    }, []);

    useEffect(() => {
        if (!mapRef.current) return;

        navigator.geolocation.getCurrentPosition((position) => {
            const currentLat = position.coords.latitude;
            const currentLng = position.coords.longitude;
            const currentCoordinate = new kakao.maps.LatLng(currentLat, currentLng); //     

            //    
            currentMarker.current = new kakao.maps.Marker({
                map: mapRef.current,
                position: currentCoordinate,
                title: " ",
                zIndex: 10 // zIndex    
            });

            //     
            mapRef.current.setCenter(currentCoordinate);

            //    
            setCurrentPosition(currentCoordinate);
        }, showErrorMsg, {
            enableHighAccuracy: true, //    
            maximumAge: 30000, // 30     
            timeout: 27000 // 27       
        });

        //      
        const mapTypeControl = new kakao.maps.MapTypeControl();
        mapRef.current.addControl(mapTypeControl, kakao.maps.ControlPosition.TOPRIGHT);

        //        
        const zoomControl = new kakao.maps.ZoomControl();
        mapRef.current.addControl(zoomControl, kakao.maps.ControlPosition.RIGHT);
    }, []);

    function showErrorMsg(error) {
        console.error(error.message);
    }

    useEffect(() => {
        let placesData = []; //      

        const searchPlaces = () => {
            if (!currentPosition) return;

            markersRef.current.forEach(marker => marker.setMap(null)); //   
            markersRef.current = []; //   

            const keyword = "";
            const options = {
                location: currentPosition,
                radius: 1000,
                sort: kakao.maps.services.SortBy.DISTANCE,
            };

            const ps = new kakao.maps.services.Places();
            ps.keywordSearch(keyword, placesSearchCB, options);
        }

        function placesSearchCB(data, status, pagination) {
            if (status === kakao.maps.services.Status.OK) {
                placesData = [...placesData, ...data]; //   
                console.log(`   : ${data.length}`);
                setPlaces(placesData.map(place => ({  // placesData  setPlaces 
                    place_name: place.place_name,
                    address_name: place.address_name,
                    distance: place.distance
                })));
                for (let i = 0; i < data.length; i++) {
                    displayMarker(data[i]);
                }
                if (pagination && pagination.hasNextPage) {
                    pagination.nextPage();
                }

            } else {
                console.error('  .');
            }
        }

        function displayMarker(place) {
            const imageSrc = markerImg;
            const imageSize = new kakao.maps.Size(34, 45);
            const markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize);
            const marker = new kakao.maps.Marker({
                map: mapRef.current, // map  useRef  
                position: new kakao.maps.LatLng(place.y, place.x),
                image: markerImage,
                title: place.place_name
            });

            const content = ReactDOMServer.renderToString(
                <CustomOverlayContent place={place} closeOverlay={closeOverlay} />
            );

            const overlay = new kakao.maps.CustomOverlay({
                content: content,
                map: mapRef.current, // map  useRef  
                position: marker.getPosition()
            });

            overlay.setMap(null);

            kakao.maps.event.addListener(marker, 'mouseover', function () {
                overlay.setMap(mapRef.current); // map  useRef  
            });

            kakao.maps.event.addListener(marker, 'mouseout', function () {
                overlay.setMap(null);
            });

            function closeOverlay() {
                overlay.setMap(null);
            }

            kakao.maps.event.addListener(marker, "click", function () {
                setRoadViewPosition({
                    lat: place.y,
                    lng: place.x,
                });
                setRoadViewPlace(place); //       
            });

            markersRef.current.push(marker); //   
        }

        searchPlaces();
    }, [currentPosition, setPlaces]);


    // /   
    const handleZoomIn = () => {
        mapRef.current.setLevel(mapRef.current.getLevel() - 1);
    };

    const handleZoomOut = () => {
        mapRef.current.setLevel(mapRef.current.getLevel() + 1);
    };

    //      
    const setMapType = (maptype) => {
        if (maptype === 'roadmap') {
            // api   MapTypeId   
            //MapTypeId.ROADMAP:      
            mapRef.current.setMapTypeId(kakao.maps.MapTypeId.ROADMAP);
        }
        //MapTypeId.SKYVIEW:      
        if (maptype === 'skyview') {
            mapRef.current.setMapTypeId(kakao.maps.MapTypeId.SKYVIEW);
        }
    };


    return (
        <div style={{ position: 'relative', width: '100%', height: '700px' }}>
            <div id="customMap" style={{ width: '100%', height: '100%' }}></div>

            <button
                onClick={handleCurrentLocation}
                style={{
                    position: 'absolute',
                    top: '2px',
                    right: '125px',
                    zIndex: 20,
                    padding: '10px',
                    backgroundColor: 'transparent',
                    border: '1px solid rgb(211, 211, 211)',
                    backgroundImage: `url(${currentLocationImg})`,
                    backgroundSize: 'cover',
                    backgroundRepeat: 'no-repeat',
                    width: '40px',
                    height: '40px',
                    cursor: 'pointer'
                }}
            >
                <span style={{ display: 'none' }}>  </span>
            </button>

            {/*   */}
            {/*<MapTypeControlBtn setMapType={setMapType} />*/}

            {/* /  */}
            {/* <ZoomControlBtn onZoomIn={handleZoomIn} onZoomOut={handleZoomOut} />*/}

            {roadViewPosition && (
                <div style={{
                    position: 'absolute',
                    top: '50%',
                    left: '50%',
                    width: '50%',
                    height: '50%',
                    transform: 'translate(-50%, -50%)',
                    zIndex: 10
                }}>
                    <RoadView
                        position={roadViewPosition}
                        place={roadViewPlace}
                        onClose={() => setRoadViewPosition(null)}
                    />
                </div>
            )}
        </div>
    );
}

export default CustomMap;